<template>
  <div id="app">
    <div class="search-box">
      <input class="" v-bind:class="objClass" type="text" 
      name="key" 
      placeholder="Your Country..."
      @click="changeActive"
      v-model="keyword"
      @keyup="changeActive">  
      <div class="suggest-search" v-bind:class="objClass" v-click-outside="clickOut">
        <div class="suggest-item" v-for="(name,index) in filteredList" v-bind:key="index" v-on:click="fetchData(name.name)">
          <div class="suggest-name">
            <p>{{ name.name }}</p>
          </div>
        </div>
      </div>
    </div>     
    <div class="result-box" v-if="typeof result.countryInfo != 'undefined'">
      <div class="country-image">
        <div class="shadow">
        </div>
        <img v-bind:src="result.countryInfo.flag" alt="">
      </div>
      <div class="country-name">
        <p>{{ result.country }}, {{ result.countryInfo.iso2 }}</p>
      </div>
      <div class="country-info">        
        <div class="population">
          <p>Dân số : <span>{{ formatNumber(result.population) }}</span></p>
        </div>
        <div class="cases">
          <p>Tổng ca nhiễm : <span>{{ formatNumber(result.cases) }}</span></p>
        </div>
        <div class="today-cases">
          <p>Số ca trong ngày : <span>{{ formatNumber(result.todayCases) }}</span></p>
        </div>
        <div class="deaths">
          <p>Tử vong : <span>{{ formatNumber(result.deaths) }}</span></p>
        </div>
        <div class="today-deaths">
          <p>Tử vong trong ngày : <span>{{ formatNumber(result.todayDeaths) }}</span></p>
        </div>
        <div class="recovered">
          <p>Số ca phục hồi : <span>{{ formatNumber(result.recovered) }}</span></p>
        </div>
        <div class="today-recovered">
          <p>Phục hồi trong ngày : <span>{{ formatNumber(result.todayRecovered) }}</span></p>
        </div>
        <div class="active-cases">
          <p>Đang điều trị : <span>{{ formatNumber(result.active) }}</span></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ClickOutside from 'vue-click-outside'
import json from './json/country.json'
export default {
  name: 'App',
  data() {
    return {
      country_data: json,
      api_url: 'https://corona.lmao.ninja/v2/countries/',
      keyword: '',
      result: {},
      isAcive: false,
    }
  },
  methods: {
    fetchData(name) {  
      this.isAcive = false
      this.keyword = ''
      fetch(`${this.api_url}${name}`)
      .then(res => {
        return res.json();
      }).then(this.setResult)       
    },
    changeActive() {
      if(this.filteredList.length <= 0) {
        this.isAcive = false
      }
      else {
        this.isAcive = true
      }
    },
    setResult(result) {
      this.result = result
    },
    formatNumber(number) {
      let new_number = new Intl.NumberFormat({ style: 'currency'}).format(number)
      return new_number
    },
    clickOut() {
      this.isAcive = false
    },
  },
  computed: {
    objClass: function() {
      return {
        active: this.isAcive
      }
    },
    filteredList() {
      return this.country_data.filter(post => {
        return post.name.toLowerCase().includes(this.keyword.toLowerCase())
      })
    }
  },
  directives: {
    ClickOutside
  }
}
</script>
